export default {"description.txt":"The artwork represents anxiety and depression.\n\nThe person’s world is broken, as shown by the\ntorn Earth in the lower left corner.\n\nAlthough he wears a smiling face, it’s just a\nmask with a crack in it, hiding his true feelings.\n\nHe is self-medicating with cigarettes and pills\nto try to make himself feel better, and he is\nfloating in space, in his own world.\nThe sunflower sun represents the role nature plays\nin helping with depression, like taking a walk\noutside, etc.\n\nThe rocket shoes represent his efforts to uplift\nhimself despite his struggles.","name.txt":"Arting Health: A Science Communication project\nby Infers Group using street art (graffiti) to\nengage the public on mental health and depression"};